
import { getAuthUserData } from "./authReduser";


let initialState = {
    initialized: false
}


export  const appReducer = (state=initialState, action) => {
    
    switch(action.type){
        case 'SETINITIALIZED':{
            //debugger
            return {
                ...state,
                initialized:true
            }
        }
        
    
    default:
        return state
    }
}
 
export function initializedSuccess() {
        return {type: 'SETINITIALIZED' }
    };

    export const initialize = () => (dispatch) =>{
        let promise = dispatch(getAuthUserData())

        promise.then(()=>{
            dispatch(initializedSuccess())
            
        })
    }