import { NavLink } from 'react-router-dom'
import classes from './Header.module.css'



export default function Header (props){
    
    return(
        <div className={classes.Header}>
        <img src={'https://s.clipartkey.com/mpngs/s/213-2139319_vigorous-piglets-and-finishers-feed-pig-icon.png'} alt=""></img>
        <p>MySocialWeb</p>
        <div className={classes.loginBlock}>
            
            { props.isAuth ? <div className={classes.loginBlock} onClick={()=>{props.logout()}}><span tooltip="click to logout" flow="left">{props.login}</span></div>
            : <NavLink to={'/login'}>Login</NavLink>
            }
            
        </div>
        </div>
    )
}