import styles from './FormsControls.module.css'

export const TextArea = (props) =>{
    
    return(
        <div className={styles.formControl+' '+styles.error}>
            <div>
            <textarea {...props.input}/>
            </div>
            {props.meta.error && props.meta.error && <span>some error</span>}
        </div>
    )
}