import React from 'react'
import classes from './Registration.module.css'
import {login} from '../../Redux/authReduser'
import { Formik } from 'formik';
import {connect} from 'react-redux'
import { Redirect } from 'react-router-dom'


const LoginForm = (props)=>{
    
    return(

    <div>
        <Formik
            initialValues={{ email: '', password: '', rememberMe: false }}
            validate={values => {
                const errors = {};
                if (!values.password) {
                    errors.password = 'Required';
                } 
                
                if (values.password.length > 21) {
                    errors.password = 'Must be 20 characters or less';
                }
                if (values.email.length > 21) {
                    errors.email = 'Must be 20 characters or less';
                }
                if (!values.email) {
                    errors.email = 'Required';
                } 
                if (
                    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                    ) {
                    errors.email = 'Invalid email address';
                }
                return errors;
            }}
            onSubmit={(formObj)=>{
                
                props.login(formObj.email, formObj.password, formObj.rememberMe)
                }}
            >
            {({
                values,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                isSubmitting
            }) => (
                <form onSubmit={handleSubmit}>
                <div className={classes.Reg}>Email:
                    <input
                        type="email"
                        name="email"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.email}
                    />
                    
                    <span className={classes.errormessage}>{errors.email && touched.email && errors.email}
                    </span>
                </div> 
                <div className={classes.Reg2}>Password: 
                    <input
                        type="password"
                        name="password"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.password}
                    />
                        <span className={classes.errormessage}>{errors.password && touched.password && errors.password}</span>
                    
                </div>
                <div className={classes.checkbox}> 
                    Remember me
                    <input type="checkbox" name="rememberMe" onChange={handleChange}
                        />
                    
                </div>
                <button type="submit" disabled={isSubmitting}>
                    üê∑
                </button>
                </form>
            )}
        </Formik>
    </div>
    )
}

 const Login=(props)=>{
    if(props.state.isAuth===true){
        return <Redirect to={'/profile'}/>
    }
return(
        <div className={classes.Registration}>
            <h1>Log in</h1>
            <LoginForm state={props.state} login={props.login}/>
        </div>
    )
}
let mapStateToProps = (state) => ({
    state: state.auth,
    
})
export default connect(mapStateToProps,{login})(Login)

