import React from 'react'
import { NavLink } from 'react-router-dom';
import classes from'./findUsers.module.css';
import Pagination from "react-js-pagination";

function FindUsers (props) {

    //let pagesCount = Math.ceil(props.props.totalUsersCount/props.props.pageSize);
    let pages=[];
    for(let i=1; i<=6; i++){
        pages.push(i)
        
    }
        return (
            
            <div className={classes.FindUsers}>
                <div className={classes.page}>
                    <Pagination
                    activeClass={classes.active}
                    activePage={props.props.currentPage}
                    itemsCountPerPage={10}
                    totalItemsCount={props.props.totalUsersCount}
                    pageRangeDisplayed={5}
                    onChange={props.onPageChanged}
                    />
                </div>

                {/* <div className={classes.page}>
                    
                    {pages.map(p => {
                        return <span key={p.id} className={props.props.currentPage === p ? classes.selectedPage : null}
                        
                        onClick={() => { props.onPageChanged(p) }}
                        >{p}</span>
                    })}
                    
                    
                </div> */}
            {
                props.props.users.map(e =>
                <div key={e.id} className={classes.User}>
                    <span>
                        <div className={classes.photo}>
                            <NavLink to={'./profile/'+e.id}>
                                <img className={classes.img} src={e.photoURL !=null ? e.photoURL : 'https://image.pngaaa.com/430/263430-small.png'} alt=''/>
                            </NavLink>
                        </div>
                        <div>
                            {
                            e.followed===false ? <button disabled={props.props.inProgress.some(id => id===e.id)} onClick={()=>{
                                props.props.tofriend(e.id)
                                }}>addFriend</button>
                            :<button disabled={props.props.inProgress.some((id) => id===e.id)} onClick={()=>{
                                props.props.tounfriend(e.id)
                                }} >UnFriend</button>
                            }
                        </div>
                    </span>
                    <div className={classes.info}>
                        
                            <div className={classes.name}>{e.name}</div>
                            {/* <div className={classes.info}>{e.location.city }</div>
                            <div className={classes.info}>{e.location.country}</div> */}
                        
                        <span>
                            <div className={classes.status}>"{e.status}"</div>
                        </span>
                    </div>
                </div>
                )
            }
            </div>
        )
    
}

export default FindUsers;
