import React from 'react'
import classes from './Posts.module.css'
import Post from './Post/Post'
import {Form, Field} from 'react-final-form'


export default function Posts (props){
    //debugger
    

    let postsElements = props.posts.map(e =>{ return <Post message={e.message} key={e.id} id={e.id} likes={e.likes} />})
    
    
    let newPost = (formObj) => {
        props.newPostContainer(formObj)
    }

    return(
        <React.Fragment>
            <div className={classes.Posts}> 
                <TextForm newPost={newPost}/> 
                {/* <button onClick={ newPost }>send</button> */}
            </div>
            
            <div><img className={classes.Images} src='https://homm3sod.ru/upload/iblock/5d4/Hero_Ufretin.png' alt="Foo eating a sandwich."/> Nikname</div>
            <div>{postsElements}</div>
        </React.Fragment>
    )
}

const TextForm = (props)=>{
    return (
        <Form onSubmit={(formObj)=>{
            props.newPost(formObj)
                console.log(formObj);}
                }>
            {({ handleSubmit }) => (
                <form onSubmit={handleSubmit}>
                <Field  component='textarea' type="textarea" placeholder='enter message' name='newMessageBody' />
                <button type='submit'>üê∑</button>
                </form>
            )}
        </Form>
    )
}