import React from 'react'
import {Route, withRouter, BrowserRouter} from  'react-router-dom'
import './App.css';
import Lefter from './Components/Lefter/Lefter';
import Registration from './Components/Registration/Registration';
import Wall from './Components/Wall/Wall';
import {connect} from 'react-redux'
import Settings from './Components/Settings/Settings';
import MessagesContainer from './Components/Messages/MessagesContainer';
import FindUsersContainer from './Components/FindUsers/FindUsersContainer';
import ContentContainer from './Components/Content/ContentContainer';
import HeaderContainer from './Components/Header/HeaderContainer';
import Login from './Components/Registration/Login';
import {initialize} from './Redux/appReduser'
import Loader from './Components/UI/Loader/Loader';
import { compose } from 'redux';
import store from './Redux/reduxStore'
import {Provider} from 'react-redux'

class App extends React.Component {
    
  componentDidMount(){
    this.props.initialize();
    
  }
  /* const [count, setCount] = useState(1);
  let registr= () => setCount(1) */
  
  render(){
    
    if(!this.props.initialized)return <Loader/>
    return (
      
        <div className="App">
          <div className="Header"><HeaderContainer  /></div>
          <div className='Lefter'><Lefter /></div>
          {
            <div>
            <Route path='/signin'><Registration registr={this.registr}/></Route>
            <Route path='/login'><Login /></Route>
            <Route path='/profile/:userId?'><ContentContainer /></Route>
            <Route path='/wall'><Wall /></Route>
            <Route path='/messages'><MessagesContainer /></Route>
            <Route path='/settings'><Settings /></Route>
            <Route path='/find'><FindUsersContainer /> </Route>
            </div>
          
          }
        </div>
      
    )
  }
}
const mapStateToProps =(state)=>({
  initialized: state.app.initialized
})

let  AppContainer = compose (
  withRouter,
  connect (mapStateToProps, {initialize}))(App)

let MainApp =(props) =>{
  return<BrowserRouter>
      <Provider store={store}>
        <AppContainer  />
      </Provider>
    </BrowserRouter>
}

export default MainApp