import { NavLink } from 'react-router-dom'
import classes from './Lefter.module.css'

export default function Lefter (){
    return(
        <div className={classes.Lefter}>
        <ul>
            <li><NavLink to='/profile'>My Page</NavLink></li>
            <li><NavLink to='/wall'>Wall</NavLink></li>
            <li><NavLink to='/messages'>Messages</NavLink></li>
            <li><NavLink to='/settings'>Settings</NavLink></li>
            <li><NavLink to='/find'>Find_User</NavLink></li>
        </ul>
        </div>
    )
}