import React from 'react'
import { NavLink} from 'react-router-dom';
import classes from'./Messages.module.css';
import { Form, Field } from 'react-final-form';
import { TextArea } from '../UI/Loader/FormsControl/FormsControls';

function Messages (props) {
    debugger
    function DialogItem  (props)  {
    return <div><NavLink to={'/messages/'+props.id}>{props.name}</NavLink></div>
}

    let dialogsItem = props.dialogs.map((i) => {
        return <DialogItem name={i.name} key={i.index} id={i.id}/>
    })

    let Message = (props) => {
    return <div>{props.m}</div>
    }
    let messageItems = props.message.map((i) => {
        return <Message m={i.message} key={i.index} />
    })

    //let textreference=React.createRef()

    function sendMessage(formObj){
        
        props.sendMessageContainer(formObj)
        
    }
    
    //if(!props.isAuth)return <Redirect to={'/login'}/>
    

        return (
            <div className={classes.Messages}>
                <div className={classes.dialog}>
                    { dialogsItem }
                    
                </div>
                <div className={classes.messageWindow}>
                    <div>{ messageItems  }</div>
                    {/* <div><textarea ref={textreference}/><button onClick={() =>sendMessage(props)}>send</button></div> */}
                    
                    <TextForm sendMessage={sendMessage}/>
                </div>
                
                
            </div>
        )
    
}
//const maxLength10=maxLength(10)

const TextForm = (props)=>{
    return (
        <Form onSubmit={(formObj)=>{
                props.sendMessage(formObj)
                console.log(formObj);}
                }
            //validate={ required, maxLength10}
            >
            {({ handleSubmit, invalid }) => (
                <form onSubmit={handleSubmit}
                >
                <Field  component={TextArea}  type="textarea" placeholder='enter message'
                name='newMessageBody' />
                <button type='submit' disabled={invalid}>üê∑</button>
                </form>
            )}
        </Form>
    )
}

export default Messages;
