export  const FindUsersReducer = (state=initialState, action) => {
    switch(action.type){
        case 'ADDFRIEND':{
            return{
                ...state,
                users: state.users.map(e =>{
                    if(e.id === action.id) {
                        return {...e, followed: true}
                    }
                    return e
                })
            }
        }
        case 'REMOVEFRIEND':{
            return{
                ...state,
                users: state.users.map(e =>{
                    if(action.id === e.id){
                        return {...e, followed: false}
                    }
                    return e
                })
            }
        }
        case 'SETUSERS':{
            
            return { ...state, users: [ ...action.users]}
        }
        case 'CURRENTPAGE':{
            
            return { ...state, currentPage: action.currentPage}
        }
        case 'TOTALUSERSCOUNT':{
            
            return { ...state, totalUsersCount: action.totalCount}
        }
        case 'ISFETCHINGTOGGLE':{
            
            return { ...state, isFetching: action.isFetching }
        }
        case 'INPROGRESS':{
            
            return { ...state,
                inProgress: action.inProgress 
                ? [...state.inProgress, action.userId]
                : [state.inProgress.filter(id=>id!==action.userId)] }
        }
    
    default:
        return state
    }
}

export function friendsAC(id) {
        return {type: 'ADDFRIEND', id  }
    };
export function unFriendsAC(id) {
        return {type: 'REMOVEFRIEND', id }
    };
export function setUsersAC(users) {
        return {type: 'SETUSERS', users }
    };
export function setCurrentPage(currentPage) {
    
        return {type: 'CURRENTPAGE', currentPage }
    };
    export function setTotalUsersCount(totalCount) {
    
        return {type: 'TOTALUSERSCOUNT', totalCount}
    };
    export function isFetchingCount(isFetching) {
        return {type: 'ISFETCHINGTOGGLE', isFetching}
    };
    export function toggleInProgress(inProgress, userId) {
        return {type: 'INPROGRESS', inProgress, userId}
    };
    
    export const getUsersCreator =(currentPage, pageSize) =>{
        return async(dispatch) =>{
            dispatch(isFetchingCount(true))
            dispatch(setCurrentPage(currentPage))
            let data= await usersAPI.getUsers(currentPage, pageSize)
                dispatch(isFetchingCount(false))
                dispatch(setUsersAC(data.items))
                dispatch(setTotalUsersCount(data.totalCount))

            
        }
    }

    export const tofriend =(userId) =>{
        return async(dispatch) =>{
            dispatch(toggleInProgress(true, userId))
            let response = await usersAPI.friend(userId)
                
                    if(response.data.resultCode===0){
                        dispatch(friendsAC(userId))
                    }
                    dispatch(toggleInProgress(false, userId))
                
        }
    }
    export const tounfriend =(userId) =>{
        return async(dispatch) =>{
            dispatch(toggleInProgress(true, userId))
            let response= await usersAPI.unfriend(userId)
                
                    if(response.data.resultCode===0){
                        dispatch(unFriendsAC(userId))
                    }
                    dispatch(toggleInProgress(false, userId))
                
        
        } 
    }