import { authAPI, loginAPI } from "../api/api";


let initialState = {
    userId: null,
    email: null,
    login:null,
    isAuth: false
}


export  const authReducer = (state=initialState, action) => {
    
    switch(action.type){
        case 'SETUSERDATA':{
            return {
                ...state,
                ...action.data
            }
        }
    
    default:
        return state
    }
}

    
    export function setUserData(userId, email, login, isAuth) {
        return {type: 'SETUSERDATA', data:{ userId, email, login, isAuth} }
    };
    export const getAuthUserData = () => (dispatch) =>{
        return authAPI.me()
        .then(response =>{
            if(response.data.resultCode===0){
                let {id, email, login} =response.data.data
                dispatch(setUserData(id, email, login, true))
            }
        })
    }
    function errorResponse(messages){
        
        alert(messages)
    }
    export const login = (email, password, rememberMe) => async(dispatch) =>{
        let response= await loginAPI.login(email, password, rememberMe)
        
            if(response.data.resultCode===0){
                dispatch(getAuthUserData())
            }
            if(response.data.resultCode!==0){
                errorResponse(response.data.messages)
            }
        
    }
    export const logout = () => async(dispatch) =>{
        let response = await loginAPI.logout()
        
            if(response.data.resultCode===0){
                dispatch(setUserData(null, null, null, false))
            }
        
    }


    