import React from 'react'
import { connect } from 'react-redux'
import { tofriend, tounfriend, setCurrentPage, 
    isFetchingCount,  getUsersCreator, toggleInProgress } from '../../Redux/FindUsersReducer'
import FindUsers from './findUsers'
import Loader from '../UI/Loader/Loader'
import { compose } from 'redux'
import { getPageSize, getUsers, getTotalUsersCount, getCurrentPage,
    getInProgress, getIsFetching, } from '../../Redux/usersSelectors'

function FindUsersContainer (props) {
    if(props.users.length===3){
    props.getUsersCreator(props.currentPage, props.pageSize);
    }
    /* if(props.users.length===3){
        props.isFetchingCount(true)
        getUsers(props.currentPage, props.pageSize).then(data =>{
            props.isFetchingCount(false)
            props.setUsers(data.items)
            props.setTotalUsersCount(data.totalCount)
            console.log(data.items)
            
        })
    }    */
    let onPageChanged = (p)=>{
        props.getUsersCreator(p, props.pageSize)
    }
    
        return (
            <>
            {
            props.isFetching=== false ? 
            <FindUsers props={props} onPageChanged={onPageChanged}/>
            :<Loader />
            }
            </>
        )
    
}


/* let mapStateToProps=(state) => {
    return {
        users: state.usersPage.users,
        pageSize: state.usersPage.pageSize,
        totalUsersCount:state.usersPage.totalUsersCount,
        currentPage: state.usersPage.currentPage,
        isFetching: state.usersPage.isFetching,
        inProgress: state.usersPage.inProgress
    }
} */
let mapStateToProps=(state) => {
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount (state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        inProgress: getInProgress(state)
    }
}

export default compose(
    //withAuthRedirect,
    connect(mapStateToProps, {
        tounfriend,
        tofriend,
        setCurrentPage: setCurrentPage,
        isFetchingCount: isFetchingCount,
        toggleInProgress,
        getUsersCreator
        })
)(FindUsersContainer)

