import { profileAPI, usersAPI } from "../api/api";

let initialState = {
    posts: [
            {message: 'Helloy idiotos!', id:1 , likes:3},
            {message: 'Why live is soo easy? 0_o', id:2 , likes:100},
        ],
        profile: null,
        status: ""
}


export  const profileReducer = (state=initialState, action) => {
    switch(action.type){
        case 'ADDPOST':{
            
            let newPost= {
                message: action.message,
                id: 5,
                likes: 0
            }
            let stateCopy={...state}
            stateCopy.posts=[...state.posts]
            stateCopy.posts.push(newPost)
            //stateCopy.action.message=''
        
            return stateCopy
        }
        case 'SETUSERPROFILE':{
            return {...state, profile:action.profile}
        }
        case 'SETSTATUS':{
            return {...state, status:action.status}
        }
    
    default:
        return state
    }
}

    export function newPostActionCreator(text) {
        return {type: 'ADDPOST', message: text }
    };
    export function setUserProfile(profile) {
        return {type: 'SETUSERPROFILE', profile }
    };
    export function setUserStatus(status) {
        return {type: 'SETSTATUS', status }
    };
    export const getUserProfile = (userId) =>(dispatch) =>{
        usersAPI.getProfile(userId)
        .then(response =>{
            dispatch(setUserProfile(response.data))
        })
    };
    export const getStatus = (userId) =>(dispatch) =>{
        profileAPI.getStatus(userId)
        .then(response =>{
            dispatch(setUserStatus(response.data))
        })
    };
    export const updateStatus = (status) =>(dispatch) =>{
        profileAPI.updateStatus(status)
        .then(response =>{
            if(response.data.resultCode===0){
            dispatch(setUserStatus(status))
        }
        })
    };



    