
let initialState = {
    dialogs: [
        {name:'Gri', id:'1'},
        {name:'Ma', id:'2'},
        {name:'Sonya', id:'3'},
        {name:'Dasha', id:'4'}],
    messages: [
        {message:'hello, how are you', id:'1'},
        {message:'bla bla bla bla', id:'2'},
        {message:'i hate this world soo much!', id:'3'}]
}


export const dialogReducer = (state=initialState, action) => {
    switch(action.type){
        case 'NEWMESSAGE': {
            
            let newMessage= {
                message: action.message,
                id: 6
            }
            return {...state,
                messages:[...state.messages, newMessage]
            }
        }
        default: return state
    }
}

export function newMessageActionCreator(text) {
    
        return {type: 'NEWMESSAGE', message: text }
        
    };
